.jumbotron {
    box-shadow: 5px 5px 7px 0px rgba(71, 71, 71, 0.83);
    background-image: url(/static/media/fixit-5-jumbotron.80aba606.png);
    background-size: cover;
    height: 500px;
    background-position: center;
    border-radius: 0%;
}

.padding{
    padding: 10px,10px,10px,10px;
}

.navbar {
    background-color: #5cab49;
}
.center{
    text-align: center;
}

.green{
    color: #5cab49;
}
.divHead{
    border-bottom: 1px solid black;
    padding-bottom: 1%;
}

h6{
    color: #5cab49;
}

.btn-primary{
    background-color: #5cab49;
    border-color: #5cab49;
} 

a{
    color: #5cab49;
}

.background{
    background-color: #5cab49;
    border-color: #5cab49;
}
.auth-wrapper {
    padding-top: 10%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: left;
}

.auth-inner {
    width: 450px;
    margin: auto;
    background: #ffffff;
    box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
    padding: 40px 55px 45px 55px;
    border-radius: 15px;
    transition: all .3s;
}
